.wcf--video-mask {
  border: 1px solid #ddd;

  .video-wrapper {
    position: relative;
    z-index: 1;
    transition: all 0.3s;
    -webkit-mask-size: 140px;
    -webkit-mask-position-x: 0;
    -webkit-mask-position-y: 0;
    -webkit-mask-repeat: no-repeat;
  }

  .video--btn {
    position: absolute;
    left: 60px;
    top: 50px;
    display: flex;
    align-items: center;
    gap: 70px;
    line-height: 1;
    z-index: 2;
    background: transparent;
    fill: #000;
    color: #000;
    border: none;
    outline: none;
  }

  .title {
    transition: all 0.3s;
    position: relative;
    z-index: 1;
    font-size: 18px;
  }

  .close-title {
    display: none;
  }

  .icon {
    z-index: 1;
    position: relative;
    line-height: 1;
    transition: all 0.3s;
    font-size: 30px;
  }

  svg {
    width: 1em;
    height: 1em;
  }

  video {
    width: 100%;
    height: 100%;
    object-fit: cover;
    pointer-events: none;
  }
}


// Open Video
.mask-open {
  .wcf--video-mask {
    .video-wrapper {
      z-index: -1;
      -webkit-mask-size: 300% !important;
      -webkit-mask-position-x: 50% !important;
      -webkit-mask-position-y: 50% !important;
      //-webkit-mask-image: url("data:image/png;base64,")!important;
    }
  }
}
