.wcf--video-mask {
  border: 1px solid #ddd;
}
.wcf--video-mask .video-wrapper {
  position: relative;
  z-index: 1;
  transition: all 0.3s;
  -webkit-mask-size: 140px;
  -webkit-mask-position-x: 0;
  -webkit-mask-position-y: 0;
  -webkit-mask-repeat: no-repeat;
}
.wcf--video-mask .video--btn {
  position: absolute;
  left: 60px;
  top: 50px;
  display: flex;
  align-items: center;
  gap: 70px;
  line-height: 1;
  z-index: 2;
  background: transparent;
  fill: #000;
  color: #000;
  border: none;
  outline: none;
}
.wcf--video-mask .title {
  transition: all 0.3s;
  position: relative;
  z-index: 1;
  font-size: 18px;
}
.wcf--video-mask .close-title {
  display: none;
}
.wcf--video-mask .icon {
  z-index: 1;
  position: relative;
  line-height: 1;
  transition: all 0.3s;
  font-size: 30px;
}
.wcf--video-mask svg {
  width: 1em;
  height: 1em;
}
.wcf--video-mask video {
  width: 100%;
  height: 100%;
  object-fit: cover;
  pointer-events: none;
}

.mask-open .wcf--video-mask .video-wrapper {
  z-index: -1;
  -webkit-mask-size: 300% !important;
  -webkit-mask-position-x: 50% !important;
  -webkit-mask-position-y: 50% !important;
}